            <span class="spano">试卷配置</span> > <span class="spant">配置考查内容与课程目标对应关系</span>
        </div>
        <div class="ToconfigureHubei-dv1">
            <h2>《{{coursename}}》课程试卷配置</h2>
            <div class="ToconfigureHubei_dv">
                <ul>
                    <li>
                        <span class="ToconfigureHubei_span1">1</span>
                        <span class="ToconfigureHubei_span2">课程目标评价依据</span>
                    </li>
                    <li style="color: #fff;">••••＞</li>
                    <li>
                        <span class="ToconfigureHubei_span3">2</span>
                        <span class="ToconfigureHubei_span2">考查内容与课程目标对应关系</span>
                    </li>
                </ul>
            </div>
        </div>
        <div style="padding-top: 29px;background-color: #fff;">
            <p class="setCourseTitle">
                <span>课程评价依据</span>
            </p>
            <div v-for="(vo,index) in examineArr" :key="index" class="examine">
                <el-row>
                    <el-col :span=" fullWidth == 1920 ? 2 : 3">
