                    </el-col>
                    <el-col :span="11">
                        <el-input v-model="vo.name" controls-position="right" placeholder="请选择考核方式"></el-input>
                        <i @click="delExamine(vo, index)" class="el-icon-circle-close del-icon hubei-close" :style="{'left': fullWidth == 1920 ? '55%' : '59%'}"></i>
                    </el-col>
                </el-row>
            </div>
            <div @click="addExamine" class="setCourseTitle add-examine-button">
                <SvgIcon icon-class="icontianjia" size="20px" class="setCourseTitle_svg"></SvgIcon>
                <span class="setCourseTitle_span">添加评价依据</span>
            </div>
        </div>
        <div class="ToconfigureHubei_ss">
            <span class="ToconfigureHubei_ss1" @click="addkaohe">将评价依据填入下表</span>
        </div>
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2"  class="demo-ruleForm">
             <el-form-item  prop="ratio">
            <div class="ToconfigureHubei-dv2" v-for="(item, indexq) in AllList" :key="indexq"> 
                <h2 class="ToconfigureHubei-dv2h2">{{item.name}}评价依据及权重</h2>
                <table cellspacing="0"  cellpadding="0"  border="0" class="el-table el-table--border">
                    <colgroup>
                        <col/>
                        <col/>
                        <col >
                    </colgroup>
                    <tr>
                        <th style=" width:50px; text-align: center;">编号</th>
                        <th style=" width:600px;text-align: center;">评价依据</th>
                        <th style=" width:200px;text-align: center;">权重</th>
                    </tr>
                    <tr v-for="(iteme, indexs) in item.goalExamList" :key="indexs">
                        <td>
                            <span  style="text-align: center;">{{indexs + 1}}</span>
                        </td>
                        <td style="text-align: center;" >
                        <el-select v-model="iteme.examId" placeholder="请选择评价依据" style="width:98%;">
                                <el-option
                                v-for="item in iteme.selected"
                                :key="item.examId"
                                :label="item.name"
                                :value="item.examId">
                                </el-option>
                            </el-select>
                        </td>
                        <td> 
                            <el-input-number v-model="iteme.ratio" controls-position="right" :min="0" :max="1" placeholder="权重"></el-input-number>
                            <i @click="delratio(iteme,indexq, indexs)" class="el-icon-circle-close del-icon" style="cursor:pointer"></i>
                        </td>
                    </tr>
                </table>
                 
                <div @click="addExamine1(indexq)" class="setCourseTitle add-examine-button">
                    <SvgIcon icon-class="icontianjia" size="20px" class="setCourseTitle_svg"></SvgIcon>
                    <span class="setCourseTitle_span">添加评价依据</span>
                </div>
                <div class="setCourseTitle tishi">* 评价依据权重总和等于1</div>
            </div>
            </el-form-item>
        </el-form>
        <div class="ToconfigureHubei-dv3">
            <template>
                <!-- <el-button type="" @click="submit()">保 存</el-button> path: "/Construction/ToconfigureHubeinext", -->
                 <div class="search--btn"  @click="submit(next = 0,'ruleForm2')" style="margin: 0 0 0 20px">保 存</div>
                <el-button type="primary" @click="submit(next = 1,'ruleForm2')" >下一步</el-button>
            </template>
        </div>
    </div>
</template>
<script>
var floatObj = function() {
    function isInteger(obj) {
        return Math.floor(obj) === obj
    }
    function toInteger(floatNum) {
        var ret = {times: 1, num: 0}
        if (isInteger(floatNum)) {
            ret.num = floatNum
            return ret
        }
        var strfi  = floatNum + ''
        var dotPos = strfi.indexOf('.')
        var len    = strfi.substr(dotPos+1).length
        var times  = Math.pow(10, len)
        var intNum = parseInt(floatNum * times + 0.5, 10)
        ret.times  = times
        ret.num    = intNum
        return ret
    }
    function operation(a, b, digits, op) {
        var o1 = toInteger(a)
        var o2 = toInteger(b)
        var n1 = o1.num
        var n2 = o2.num
        var t1 = o1.times
        var t2 = o2.times
        var max = t1 > t2 ? t1 : t2
        var result = null
        switch (op) {
            case 'add':
                if (t1 === t2) { // 两个小数位数相同
                    result = n1 + n2
                } else if (t1 > t2) { // o1 小数位 大于 o2
                    result = n1 + n2 * (t1 / t2)
                } else { // o1 小数位 小于 o2
                    result = n1 * (t2 / t1) + n2
                }
                return result / max
            case 'subtract':
                if (t1 === t2) {
                    result = n1 - n2
                } else if (t1 > t2) {
                    result = n1 - n2 * (t1 / t2)
                } else {
                    result = n1 * (t2 / t1) - n2
                }
                return result / max
            case 'multiply':
                result = (n1 * n2) / (t1 * t2)
                return result
            case 'divide':
                result = (n1 / n2) * (t2 / t1)
                return result
        }

    }
    // 加减乘除的四个接口
    function add(a, b, digits) {
        return operation(a, b, digits, 'add')
    }
    function subtract(a, b, digits) {
        return operation(a, b, digits, 'subtract')
    }
    function multiply(a, b, digits) {
        return operation(a, b, digits, 'multiply')
    }
    function divide(a, b, digits) {
        return operation(a, b, digits, 'divide')
    }
    return {
        add: add,
        subtract: subtract,
        multiply: multiply,
        divide: divide
    }

}();
export default {
    name: "TestWorld",
    
    data(){
        var validatePass = (rule, value, callback) => {
            let self = this
            function recursiveCheck(arr) {
            let sum = 0;
            for (let vo of arr) {
                const ratio= vo.ratio || 0
                sum=floatObj.add(sum, ratio)
            }
            if (sum === 1) {
                return "next";
            } else {
                return callback(new Error("评价依据权重总和等于1"));
            }
            }
            for (let i = 0, length = self.AllList.length; i < length; i++) {
            if (recursiveCheck(self.AllList[i].goalExamList) === "next") {
                if (self.AllList[i + 1]) {
                recursiveCheck(self.AllList[i + 1].goalExamList);
                } else {
                callback();
                break;
                }
            }
            }
        };
        return{
            fullWidth:document.documentElement.clientWidth, //计算屏幕的宽度
            content:'',
            AllList:[],
            videolist:[],
            course_id:this.$route.query.id,
            coursename:this.$route.query.name,
            type:this.$route.query.type,
            grade:this.$route.query.grade,
            scheduId:this.$route.query.scheduId,
            examineArr: [{
                name: null,
                examId:null
            }],
            selected:[],
            ruleForm2: {
                pass: '',
                checkPass: '',
                age: ''
            },
            rules2: {
                ratio: [
                    { validator: validatePass, trigger: 'blur' }
                ],
            }
        }
    },
     mounted(){
        this.getexamgoal()
    },
    methods:{
        //  数据回显
        getexamgoal(){
            //const url=`/basic/courseGoal/examCourseGoalList/${this.$route.query.id}`
            //2020.3.10改参数后面拼接年级grade
            const url=`/basic/courseGoal/examCourseGoalList/${this.$route.query.id}/${this.$route.query.scheduId}`
            this.$get(url).then(res=>{
                if(res.status == 1){
                    this.AllList = res.data.list || []
                    this.examineArr = res.data.examList
                    // this.getTraExaminePatternDTO( res.data.examList)
                }
            })
        },
        // 获取考核方式
        getTraExaminePatternDTO(arr){
            let list = []
             this.examineArr.forEach(element => {
                 list.push({
                    courseId:this.$route.query.id,
                    scheduId:this.$route.query.scheduId,
                    name:element.name,
                    id:element.examId
                 })
            });
            this.$post('/basic/examine/addExamine',list).then(res=>{
                if(res.status === 1){
                    // this.selected = res.data
                    // this.AllList.forEach(element => {
                    //     element.goalExamList.forEach(vo => {
                    //         vo.selected = this.selected 
                    //     });
                    // });
                    setTimeout(()=>{
                       this.getexamgoal()
                    },600)
                   
                }
            })
        },
        addkaohe(){
            this.getTraExaminePatternDTO()
        },
        addExamine(indexq) {
            this.examineArr.push({
                name: null,
                id:null
            })
        },
        addExamine1(indexq) {
            this.AllList[indexq].goalExamList.push({
                selected: null,
                ratio: null
            })
            this.AllList[indexq].goalExamList.forEach(element => {
                element.selected=this.examineArr
            });
        },
        setCourseDel(id, index) {
            this.$deletes(`/basic/examine/delExamine/${id}/${this.$route.query.id}`).then(res => {
                if (res.status === 1) {
                    this.$notify.success({
                        title: "success",
                        duration: 1500,
                        message: "删除成功"
                    });
                    this.getexamgoal()
                    //  this.examineArr.splice(index, 1)
                } else {
                    this.$notify.error({
                        title: "error",
                        duration: 1500,
                        message: res.message
                    })
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        setCourseDel1(id,indexq, index) {
            this.$get(`/basic/courseGoal/delExamCourseGoal/${id}`).then(res => {
                if (res.status === 1) {
                    this.$notify.success({
                        title: "success",
                        duration: 1500,
                        message: "删除成功"
                    });
                     this.getexamgoal()
                    // this.AllList[indexq].goalExamList.splice(index, 1)
                } else {
                    this.$notify.error({
                        title: "error",
                        duration: 1500,
                        message: res.message
                    })
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        delExamine(vo, index) {
            if (vo.examId) {
                this.setCourseDel(vo.examId, index)
            } else {
                this.$notify.success({
                    title: "success",
                    duration: 1500,
                    message: "删除成功"
                });
               this.examineArr.splice(index, 1)
            }
        },
        delratio(vo, indexq,index){
            if (vo.id) {
                this.setCourseDel1(vo.id,indexq, index)
            } else {
                this.$notify.success({
                    title: "success",
                    duration: 1500,
                    message: "删除成功"
                });
                this.AllList[indexq].goalExamList.splice(index, 1)
            }
        },
        //提交按钮
        submit(next,formName){
            let list= [];
            this.AllList.forEach((item,index) => {
                item.goalExamList.forEach(items => {
                    list.push({
                            examId:items.examId ,
                            courseGoalId:item.goalId,
                            ratio:items.ratio,
                            id:items.id || '',
                            scheduId:this.$route.query.scheduId
                        })
                })
            });
            
            if(next ===0 && this.examineArr.length ===0 && list.length ===0){
                   this.$refs[formName].resetFields()
                   this.userSave(next, list)
                   return
            }
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.userSave(next, list)
                }else {  
                    this.$notify.warning({
                        title: 'warning',
                        duration:1500,
                        message: '评价依据权重总和等于1'
                    })
                }
            });
            
        },
        userSave(next, postData){
            this.$post('/basic/courseGoal/addExamCourseGoal',postData).then(res=>{
                if(res.status === 1){
                    if(next === 0){
                        this.getexamgoal()
                        this.$notify.success({
                            title: 'success',
                            duration:1500,
                            message: '保存成功'
                        })
                    }else {
                        this.$router.push({path: '/Construction/ToconfigureHubeinext',
                            query: {
                                id: this.$route.query.id,
                                coursename:this.$route.query.name,
                                scheduId:this.$route.query.scheduId
                                // teacherId:localStorage.getItem('userId'),
                                // grade:row.grade
                            }
                        }); 
                    }
                    
                }else {
                    this.$notify.error({
                        title: 'error',
                        duration:1500,
                        message: res.message
                    })
                }
            })
        }
    }
}
</script>
